*, *::before ::after{
  box-sizing: border-box;
}

body{
  background: linear-gradient(to bottom, #00141f, #ddccbc);
  height: 100vh;
}
.App{
  height: 100vh;
  width: 100%;
}
.grid{
  display: grid;
  justify-content: center;
  align-content: center;
  min-height: 80vh;
  grid-template-columns: repeat(4, 80px);
  grid-template-rows: minmax(80px, auto) repeat(5, 80px);
  row-gap: 3px;
  column-gap: 3px;
}
.grid button{
  border-radius: 50px;
  cursor: pointer;
  font-size: 1.5rem;
  font-weight: bold;
  border: 3px solid #95989d;
  color: whitesmoke;
  outline: none;
  background-color: rgba(1, 1, 27, 0.336);
  transition: all 0.5s;
}
.grid button:hover{
  background-color: #c6b9ab;
}
.span-two{
  grid-column: span 2;
}
.output {
  grid-column: 1 /-1;
  margin-bottom: .4rem;
  background-color: #00141f;
  display: flex;
  align-items: flex-end;
  justify-content: space-around;
  flex-direction: column;
  border-radius: 1rem;
  padding: 10px;
  word-wrap: break-word;
  word-break: break-all;
}
.output .prev{
  color: rgb(5, 23, 185);
  font-size: 1.5rem;
}
.output .current{
  color: whitesmoke;
  font-size: 2rem;
}